.navBar {
  width: 100%;
  height: 3.5rem;
  background-color: rgba(255, 255, 255, 0.8);
  color: white;
  position: fixed;
  display: flex;
  padding-left: 10px;
  padding-right: 10px;
  justify-content: space-between;
}

.nav-links {
  display: flex;
  list-style: none;
  justify-content: space-evenly;
  height: 100%;
  margin-top: 0;
}

.nav-links li {
  padding: 0 20px;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  height: 100%;
  cursor: pointer;
}

.nav-links li a {
  color: black;
  font-weight: bold;
  text-decoration: none;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  display: block;
  cursor: pointer;
  position: relative;
}

.nav-links li:hover {
  background-color: #f5183d;
}

.nav-links li:hover a {
  color: white;
}

.nav-links .active {
  background-color: #f5183d;
}

.nav-links .active a {
  color: white;
}

.drawer {
  width: 100%;
  margin: 20px;
}

.drawer-btn {
  display: none;
  font-size: 30px;
  color: white;
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  width: 50px;
  height: 50px;
}

.drawer-btn:focus {
  outline: none;
}

@media (max-width: 600px) {
  .navBar {
    padding-left: 0px;
    padding-right: 0px;
    justify-content: space-between;
  }
  .nav-links {
    display: block;
    list-style: none;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    width: 100%;
    margin-top: 15px;
    margin: 15px auto;
  }
  .nav-links li {
    margin: 0 auto;
    text-align: center;
    height: 40px;
    width: 100%;
  }
  .drawer-btn {
    display: block;
    margin: auto;
  }
  .show-nav {
    display: flex;
    flex-direction: column;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
  }
  .hide-nav {
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    display: none;
  }
  .secondary-btn {
    background-color: #f5183d;
    border: none;
    color: white;
    padding: 3px 12px;
    margin: 0.5rem 1rem;
    font-size: 0.95rem;
    border-radius: 5px;
    font-weight: lighter;
    cursor: pointer;
    -webkit-transition: all  0.5s;
    transition: all  0.5s;
    display: flex;
    height: 1.8rem;
    font-size: 0.8rem;
    flex-direction: column;
    margin: auto;
    align-items: center;
    justify-content: center;
  }
  .secondary-btn:hover {
    background-color: rgba(255, 255, 255, 0.9);
    color: #f5183d;
  }
}

.pricing-table {
